	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Twitter","date":"13 Mar 2007","slug":"/2007-03-13-twitter/","tags":"Y2007"},"html":"<p>On the new latest <a href=\"http://twitter.com/\">thing</a>...</p>\n<blockquote><p>\n  In an age where style easily trumps content, Twitter has neither.\n</p></blockquote>\n<p><a href=\"http://www.semergence.com/2007/03/12/twitter-is-dumb/\">Seth Ladd</a> (via <a href=\"http://burningbird.net/fooflah/pastel-fruit-flavored-marshmallows/\">Burningbird</a>)</p>"},"next":{"fields":{"title":"Irritable and Distracted","date":"13 Mar 2007","slug":"/2007-03-13-irritable-and-distracted/"}},"previous":{"fields":{"title":"Redubbing Cartoons","date":"15 Mar 2007","slug":"/2007-03-15-redubbing-cartoons/"}}},{"node":{"fields":{"title":"Irritable and Distracted","date":"13 Mar 2007","slug":"/2007-03-13-irritable-and-distracted/","tags":"Y2007"},"html":"<p>Sorry for the lack of subtance here lately. (Actually, that's just a phrase. I'm not really sorry at all.) There are a couple of explanations.</p>\n<ul>\n<li>I'm irritable. I'm losing weight (again). I can lose weight like other people can stop smoking. It's so easy, I've done it dozens of times. However, this time I've just finished a huge bout of <a href=\"http://www.papascott.de/archives/2007/01/08/needles-and-pins/\">negative motivation</a>, and there's no way I ever want to go through that again. Hopefully that's what it will take to make it stick. But in the meantime I'm grumpy.</li>\n<li>\n<p>I'm distracted. There's something very huge and very good that's going to happen this year that's going to mean big changes for all of us in the PapaScott household. But I can't talk about it yet, except to say that it in no way involves childbirth.</p>\n</li>\n</ul>\n<p><strong>Update 27 Mar 2007:</strong> I made this post private after the first four comments.  Not that any of them were close to guessing the secret, but anyone who knew us personally would have immediately known what the \"big change\" was. The secret will be released today, so I've made the post public again.</p>"},"next":{"fields":{"title":"Kids Marketing","date":"08 Mar 2007","slug":"/2007-03-08-kids-marketing/"}},"previous":{"fields":{"title":"Twitter","date":"13 Mar 2007","slug":"/2007-03-13-twitter/"}}},{"node":{"fields":{"title":"Kids Marketing","date":"08 Mar 2007","slug":"/2007-03-08-kids-marketing/","tags":"Y2007"},"html":"<p><a href=\"http://www.flickr.com/photos/papascott/414553803/\" title=\"Photo Sharing\"><img src=\"http://farm1.static.flickr.com/175/414553803_908de3943f_m.jpg\" width=\"240\" height=\"180\" alt=\"08032007078\" /></a></p>\n<p>This may look like leftover spaghetti. But it's really volcano noodles with lava sauce with stone pieces.</p>\n<p>Marketing is everything.</p>"},"next":{"fields":{"title":"This Time Nationwide","date":"06 Mar 2007","slug":"/2007-03-06-this-time-nationwide/"}},"previous":{"fields":{"title":"Irritable and Distracted","date":"13 Mar 2007","slug":"/2007-03-13-irritable-and-distracted/"}}}],"pathPrefix":"","first":false,"last":false,"index":149,"pageCount":1101}}

